.content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    text-align: center;
    min-height: 9vh;
}
.more {
    flex-wrap: wrap;
}

.content-item {
    margin-bottom: 5px;
    transition: .6s;
    font-size: 35px;
}

.content-item:hover {
    cursor: pointer;
    opacity: .5;
}

.content-inner-div {
    width: 100%;
    height: 100%;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
// .content-item-par {
    // font-size: 35px;
// }


.contact-data {
    padding: 0;
    margin: 0;
    height: inherit;
}

.wrap {
    display: flex;
    align-items: center;
    justify-content: center;
}